.wrapper {
    margin: 60px auto;
    display: flex;
}
.logo {
    .img {
        width: 100%;
    }
}
.content {
    display: flex;
    align-items: center;
    justify-content: center;
}

.body {
    width: 60%;
    display: flex;
    flex-direction: column;

    .heading {
        font-size: 4rem;
        font-weight: 700;
        text-align: center;
        margin-bottom: 24px;
    }

    .title {
        font-size: 2.2rem;
        font-weight: 600;
        text-align: center;
        margin-bottom: 12px;
    }

    .link {
        color: var(--primary);
        display: block;
        text-align: center;
        font-size: 2rem;

        &:hover {
            opacity: 0.5;
        }
    }
}
